body {
  visibility: hidden;
  opacity: 0;
}
section {
  position: relative;
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
  background-color: black;
  color: white;
}

.intro {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  pointer-events: none; 
  user-select: none;

  .panel {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    transform-origin: bottom;
    background-color: white;
  }
}

.hero-container {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 90%;
  transform: translate(-50%, -50%);
  filter: saturate(0) brightness(83%);

  img {
    height: 100%;
  }
}

.title-container {
  z-index: 999;
  position: relative;
  color: white;
  mix-blend-mode: difference;
}

h1 {
  // font-family: "Nova Flat", cursive;
  font-size: 14.5vw;
  text-align: center;
  user-select: none;
}

p {
  max-width: clamp(500px, 90%, 720px);
  margin: 0 auto;
  line-height: 1.1;
  font-size: clamp(20px, 4.4vw, 50px);
  text-align: center;
  user-select: none;
}

.JL {
  z-index: 999;
  position: absolute;
  top: 19.5%;
  font-size: 7vw;
  letter-spacing: 35px;
  text-align: center;
  user-select: none;
}

.img-container {
  position: relative;
  height: 95%;
  width: 100%;
  overflow: hidden;
  // filter: saturate(0);

  .mask {
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    transform-origin: bottom;
  }

  .img {
    height: 100%;
    width: 100%;
    background-image: url("https://images.pexels.com/photos/2443590/pexels-photo-2443590.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
}

